namespace $ {
	export class $hyoo_harp_app extends $mol_page {
		
		/**
		 * ```tree
		 * title \HARP - Humane API REST Protocol
		 * ```
		 */
		title() {
			return "HARP - Humane API REST Protocol"
		}
		
		/**
		 * ```tree
		 * plugins / <= Theme
		 * ```
		 */
		plugins() {
			return [
				this.Theme()
			] as readonly any[]
		}
		
		/**
		 * ```tree
		 * tools /
		 * 	<= Source
		 * 	<= Lights
		 * ```
		 */
		tools() {
			return [
				this.Source(),
				this.Lights()
			] as readonly any[]
		}
		
		/**
		 * ```tree
		 * body / <= Content
		 * ```
		 */
		body() {
			return [
				this.Content()
			] as readonly any[]
		}
		
		/**
		 * ```tree
		 * Theme $mol_theme_auto
		 * ```
		 */
		@ $mol_mem
		Theme() {
			const obj = new this.$.$mol_theme_auto()
			
			return obj
		}
		
		/**
		 * ```tree
		 * Source $mol_link_source uri \https://github.com/hyoo-ru/harp.hyoo.ru
		 * ```
		 */
		@ $mol_mem
		Source() {
			const obj = new this.$.$mol_link_source()
			
			obj.uri = () => "https://github.com/hyoo-ru/harp.hyoo.ru"
			
			return obj
		}
		
		/**
		 * ```tree
		 * Lights $mol_lights_toggle
		 * ```
		 */
		@ $mol_mem
		Lights() {
			const obj = new this.$.$mol_lights_toggle()
			
			return obj
		}
		
		/**
		 * ```tree
		 * rate 0
		 * ```
		 */
		rate() {
			return 0
		}
		
		/**
		 * ```tree
		 * Rate $mol_speck value <= rate
		 * ```
		 */
		@ $mol_mem
		Rate() {
			const obj = new this.$.$mol_speck()
			
			obj.value = () => this.rate()
			
			return obj
		}
		
		/**
		 * ```tree
		 * uri? \pullRequest(state=closed=merged=;+repository(name;private;owner(name);_len(issue));-updateTime;author(name);_num=20@30=)
		 * ```
		 */
		@ $mol_mem
		uri(next?: any) {
			if ( next !== undefined ) return next as never
			return "pullRequest(state=closed=merged=;+repository(name;private;owner(name);_len(issue));-updateTime;author(name);_num=20@30=)"
		}
		
		/**
		 * ```tree
		 * Uri $mol_textarea
		 * 	hint \harp;query
		 * 	value? <=> uri?
		 * ```
		 */
		@ $mol_mem
		Uri() {
			const obj = new this.$.$mol_textarea()
			
			obj.hint = () => "harp;query"
			obj.value = (next?: any) => this.uri(next)
			
			return obj
		}
		
		/**
		 * ```tree
		 * json? null
		 * ```
		 */
		@ $mol_mem
		json(next?: any) {
			if ( next !== undefined ) return next as never
			return null as any
		}
		
		/**
		 * ```tree
		 * Json $mol_dump_value value <= json?
		 * ```
		 */
		@ $mol_mem
		Json() {
			const obj = new this.$.$mol_dump_value()
			
			obj.value = () => this.json()
			
			return obj
		}
		
		/**
		 * ```tree
		 * Content $mol_list rows /
		 * 	<= Rate
		 * 	<= Uri
		 * 	<= Json
		 * ```
		 */
		@ $mol_mem
		Content() {
			const obj = new this.$.$mol_list()
			
			obj.rows = () => [
				this.Rate(),
				this.Uri(),
				this.Json()
			] as readonly any[]
			
			return obj
		}
	}
	
}

