namespace $ {
	export class $hyoo_harp_demo extends $mol_example_large {
		
		/**
		 * ```tree
		 * sub / <= Sandbox
		 * ```
		 */
		sub() {
			return [
				this.Sandbox()
			] as readonly any[]
		}
		
		/**
		 * ```tree
		 * tags /
		 * 	\API
		 * 	\Query
		 * 	\REST
		 * ```
		 */
		tags() {
			return [
				"API",
				"Query",
				"REST"
			] as readonly any[]
		}
		
		/**
		 * ```tree
		 * aspects /
		 * 	\Application
		 * 	\Language/HARP Query
		 * 	\Algorithm/Parse
		 * ```
		 */
		aspects() {
			return [
				"Application",
				"Language/HARP Query",
				"Algorithm/Parse"
			] as readonly any[]
		}
		
		/**
		 * ```tree
		 * title
		 * ```
		 */
		title() {
			return this.Sandbox().title()
		}
		
		/**
		 * ```tree
		 * Sandbox $hyoo_harp_app
		 * 	Lights null
		 * 	title => title
		 * ```
		 */
		@ $mol_mem
		Sandbox() {
			const obj = new this.$.$hyoo_harp_app()
			
			obj.Lights = () => null as any
			
			return obj
		}
	}
	
}

